@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.hide {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

body {
  background-color: #fff;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: white;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  background-color: #c0dbd9;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  margin-bottom: auto;
}

.blue-block {
  background-color: #7faff6;
  min-height: 100px;
  padding: 30px;
  &--header {
    margin-bottom: 15px;
  }
}

.btn {
  padding: 8px 15px;
  border-radius: 5px;
  border: 0;
  background-color: #a5c7f9;
  cursor: pointer;
  outline: 0;
  transition: background-color 0.3s;

  &:focus {
    box-shadow: 0 0 10px rgb(0, 0, 0);
  }
  &:hover {
    background-color: #bed5f8;
  }
  
  &:active {
    background-color: #94b0da;
    transition: background-color 0.1s;
  }
}

.green-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #b3f95a;
  font-size: 53px;
  line-height: 62px;
  margin-bottom: 15px;

  &__item {
    background-color: #8ad191;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 198px;
    width: 33.3%;
    margin-bottom: 15px;
  }

  &--2 {
    width: 50.3%;
  }
  &--3 {
    width: 13.6%;
  }

  &--4 {
    width: 85%;
    margin-bottom: 0;
  }

  &--5 {
    margin-bottom: 0;
    width: 13.6%;
  }
}

.block2 {
  background-color: #88b5bf;
  display: flex;
  justify-content: space-between;
  padding: 30px;
  margin-bottom: 15px;

  &__item {
    width: 23%;
    height: 0;
    padding-bottom: 18%;
    background-color: #0d89b0;
  }
}

.popap {
  position: fixed;
  top: 50%;
  left: 150%;

  width: 400px;
  background-color: white;
  color: #000;
  font-size: 53px;
  line-height: 62px;
  transform: translate(-50%, -50%) scale(0);
  overflow: hidden;
  transition: transform 0.4s, left 0.4s 0.1s;

  &--active {
    transform: translate(-50%, -50%) scale(1);
    left: 50%;
    transition: transform 0.5s 0.1s, left 0.4s;
  }

  &__body {
    position: relative;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__close {
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 23px;
    padding: 8px 15px;
    background-color: #7faff6;
    color: white;
    border: 0;
    border-radius: 5px;
  }
}

.contact-me {
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 55;
  border-radius: 25px;
  padding-left: 10px;
  @include line-clamp();
  transform: translateX(-100vw);
  animation: contact-me 15s 0s 1 forwards;

  &__text {
    font-size: 16px;
    color: white;
  }

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    width: 50px;
    height: 50px;
    transform: translateY(-50%);
    background-image: url(../images/sprite.svg#telegram);
    background-repeat: no-repeat;
    background-size: contain;
  }
}

